export default function Portfolio(props) {
  const pageData = getPageData(props)
  const PortfolioPagesData = usePortfolioAllSinglePages()

  return (
    <Layout className="flex-col">
      <Seo pageData={pageData} />
      <Sidebar />
      <MainNav />
      <HomePageCover style={{ height: "400px" }} bg={false}>
        <div
          className="flex flex-col text-center absolute z-10 text-hight-contrast max-w-screen-sm"
          style={{ top: "24%" }}
        >
          <h1 className="xxxLarge-high-title font-bold mb-4">Portfolio</h1>
          <span className="xxxLarge-h1 text-text-secondary">
            I have posted less than 10% of my works. You can contact me for more
            examples.
          </span>
        </div>
      </HomePageCover>

      <section className="grid gap-4 mx-4 mt-4 md:grid-cols-2 sm:grid-cols-1">
        {PortfolioPagesData.map(({ node }) => (
          <a
            href={`/${node.slug.replace(/content\//, "")}`}
            className="item mb-14 text-center"
            key={node.slug}
          >
            <GatsbyImage
              alt={node.frontmatter.title}
              image={getImage(node.frontmatter.cover)}
              className="rounded bg-background-primary"
              style={{
                boxShadow: `1px 0px 0px var(--separator-non-opaque),
              -1px 0px 0px var(--separator-non-opaque),
                0px 1px 0px var(--separator-non-opaque),
                0px -1px 0px var(--separator-non-opaque)`,
              }}
            />
            <h2 className="text-text-secondary m-5">
              {node.frontmatter.caption}
            </h2>
          </a>
        ))}
      </section>
    </Layout>
  )
}
